<template>
  <div>
    <!-- header begin -->
    <div class="nav-bar">
      <div class="nav-bar-left" @click="goHome()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-title">请假申请信息</div>
      <div class="nav-bar-right" @click="showSearchPopup()">
        <i class="iconfont iconsousuo" />
      </div>
    </div>
    <!-- header end -->

    <div :class="showRemind ?'container':'container-tab' ">
      <!-- 温馨提醒 begin -->
      <div class="remind-holidays" :class="showRemind ?'hide':'' ">
        <div class="remind-tit"><i class="iconfont icontongzhi1" /> 温馨提醒：</div>
        <div class="remind-content">
          1、婚假(5天)、产假(4个月)、陪产假(5天)、丧假(5天)为带薪假期，不视为请假，超过标准天数的按事假处理。<br>
          2、如果上午请假，下午正常上班，请在APP上申报下午的工作状态。
          3、丧假： 员工及配偶父母、子女、配偶去世，可集中休一次5个工作日丧假。
        </div>
        <div class="remind-close" @click="showRemindBtn()"><i class="iconfont iconguanbi" /></div>
      </div>
      <!-- 温馨提醒 end -->
      <div class="container-mini">

        <div class="content">
          <div class="common-main box">
            <div class="holidays-flex">
              <div class="tit">{{ new Date().getFullYear() }}年剩余年假</div>
              <div class="date">{{ synj?synj:0 }}天</div>
            </div>
          </div>
        </div>
        <common-list
          ref="commonlist"
          class="common-list"
          :post-url="list.postUrl"
          :req-data="list.reqData"
          :data-name="list.dataName"
          :first-fresh="true"
          :apptype-show="false"
          :no-data-show="false"
          @returnData="getPrizeLog($event)"
        >
          <!-- 循环列表 begin -->
          <van-swipe-cell v-for="(item,index) of qjxxList" :key="index">
            <div class="content" @click="itemClick(item)">
              <div class="common-main box overflow-hidden relative">
                <div class="title">
                  <div class="title-left">
                    <h2>{{ item.title }}</h2>
                  </div>
                </div>
                <div v-if="item.zt===0 || item.zt===1 || item.zt===2" class="askleave green"><span>{{ qjztMap[item.zt] }}</span></div>
                <div v-else class="askleave red"><span>{{ qjztMap[item.zt] }}</span></div>
                <div class="page-list">
                  <div class="pagelist-left">请假申请人</div>
                  <div class="pagelist-right">{{ item.sqrmc }}</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">提交时间</div>
                  <div class="pagelist-right">{{ formDate(item.cjsj) }}</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">请假天数(天)</div>
                  <div class="pagelist-right">{{ item.qjts }}天</div>
                </div>
                <div v-if="item.dxnjts" class="page-list">
                  <div class="pagelist-left">抵扣年假天数(天)</div>
                  <div class="pagelist-right">{{ item.dxnjts }}天</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">状态</div>
                  <div class="pagelist-right">{{ qjztMap[item.zt] }}</div>
                </div>
                <!-- <div class="page-list">
                  <div class="pagelist-left">请假时间</div>
                  <div class="pagelist-right">2021/07/02 ( 下午 ) <br> - <br> 2021/07/02 ( 下午 )</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">请假天数(天)</div>
                  <div class="pagelist-right">0.5天</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">创建时间</div>
                  <div class="pagelist-right">2021/07/02 11:00:44</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">状态</div>
                  <div class="pagelist-right">提交审批</div>
                </div>
                <div class="page-list">
                  <div class="pagelist-left">是否抵消年假</div>
                  <div class="pagelist-right">是</div>
                </div> -->
              </div>
            </div>
            <template #right>
              <div class="sideslip">
                <!-- <div class="sideslip-item green" @click="goQjxxXq(item)">
                  <div class="icon"> <i class="iconfont iconqjxq" /> </div> 详情
                </div>
                <div v-if="item.zt===0 || item.zt===3" class="sideslip-item" @click="editQjxx(item)">
                  <div class="icon"> <i class="iconfont iconxieruqianming" /> </div> 修改
                </div> -->
                <div v-if="item.zt===-1 || item.zt===0 || item.zt===3" class="sideslip-item red" @click="deleteQjxx(item)">
                  <div class="icon"> <i class="iconfont iconshanchu1" /> </div> 删除
                </div>
              </div>
            </template>
          </van-swipe-cell>
        <!-- 循环列表 end -->
        </common-list>
      </div>
    </div>

    <!-- 查询条件  begin -->
    <van-popup v-model="SeachShow" position="top" class="search-popup" overlay-class="transparent-overlay">
      <div class="common-main box">
        <div class="spe-input">
          <!-- <van-cell title="请假类型" :value="leixing" is-link @click="QjlxShow = true" />-->
          <van-cell title="请假状态" :value="zhuangtai" is-link @click="QjztShow = true" />
          <van-cell title="请假发起时间" :value="fqsjShow" is-link @click="show = true" />
          <div class="search-popup-btn">
            <van-button type="info" round size="large" plain @click="reset()">重置</van-button>
            <van-button type="info" round size="large" @click="search()">搜索</van-button>
          </div>
        </div>
      </div>
    </van-popup>
    <!-- 查询条件  end -->
    <van-calendar v-model="show" :min-date="minDate" color="#1890ff" @confirm="onConfirm" />
    <!-- 请假类型弹窗 beign -->
    <van-popup v-model="QjlxShow" round position="bottom">
      <van-picker
        title="请假类型"
        show-toolbar
        :columns="Qjlxcolumns"
        @confirm="onQjlxConfirm"
        @cancel="QjlxShow = false"
        @change="onChange1"
      />
    </van-popup>
    <!-- 请假类型弹窗 end -->

    <!-- 请假状态弹窗 beign -->
    <van-popup v-model="QjztShow" round position="bottom">
      <van-picker
        title="请假状态"
        show-toolbar
        :columns="Qjztcolumns"
        @confirm="onQjztConfirm"
        @cancel="QjztShow = false"
        @change="onChange2"
      />
    </van-popup>
    <!-- 请假状态弹窗 end -->

    <div class="common-space" />

    <!-- 底部按钮 begin -->
    <div class="foot-btn fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="goZjQjxx()">新增</van-button></li>
      </ul>
    </div>
    <!-- 底部按钮 end -->
  </div>
</template>

<script land="script" scoped>
import commonList from '@/components/list/commonList1.vue'
export default {
  name: 'TbGzrz',
  components: {
    commonList
  },
  data() {
    return {
      show: false,
      fqsjShow: '请选择时间',
      fqsj: '',
      qjztMap: {
        0: '初始化',
        1: '提交审批',
        2: '审批通过',
        '-1': '已撤销',
        3: '退回'
      },
      qjztMap1: {
        '初始化': 0,
        '提交审批': 1,
        '审批通过': 2,
        '已撤销': -1,
        '退回': 3,
        '全部': '',
        '请选择状态': ''
      },
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      qjlxMap: {
        'BSJ': '事假',
        'NJ': '年假',
        'HJ': '婚假',
        'CJ': '产假',
        'PCJ': '陪产假',
        'SJ': '丧假',
        'TX': '调休',
        'PKJ': '陪考假'
      },
      headerData: {
        title: '请假申请信息',
        leftUrl: 'back',
        leftText: ' '
      },
      qjxxList: [],
      synj: '',
      SeachShow: false,
      QjlxShow: false,
      showRemind: false,
      QjztShow: false,
      leixing: '选择考勤类型',
      zhuangtai: '请选择状态',
      Qjlxcolumns: ['全部', '事假', '婚假', '产假', '陪产假', '丧假', '调休'],
      Qjztcolumns: ['全部', '初始化', '提交审批', '审批通过', '退回', '已撤销'],
      minDate: new Date(1999, 0, 1)
    }
  },
  created() {
    this.getSynj()
    this.getQjxx()
  },
  methods: {
    itemClick(item) {
      if (item.zt === 0 || item.zt === -1 || item.zt === 3) {
        this.editQjxx(item)
      } else {
        this.goQjxxXq(item)
      }
    },
    editQjxx(qjxx) {
      this.$router.push({
        name: 'ZjQjxx',
        params: {
          qjxx: qjxx,
          title: '修改请假信息'
        }
      })
    },
    reset() {
      this.zhuangtai = '请选择状态'
      this.fqsjShow = '请选择时间'
      this.fqsj = ''
    },
    search() {
      this.SeachShow = false
      this.$refs.commonlist.onRefresh({
        ygbh: this.$store.state.mh.mhUserInfo.id,
        fqsj: this.fqsj,
        qjbh: '',
        zt: this.qjztMap1[this.zhuangtai]
      })
    },
    onConfirm(date) {
      this.fqsjShow = this.formatDate(date)
      this.fqsj = this.formatDate1(date)
      this.show = false
    },
    formatDate(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
    },
    formatDate1(date) {
      const year = date.getFullYear()
      let month = ''
      let day = ''
      if ((date.getMonth() + 1) < 10) {
        month = '0' + (date.getMonth() + 1)
      } else {
        month = date.getMonth() + 1
      }
      if (date.getDate() < 10) {
        day = '0' + (date.getDate())
      } else {
        day = date.getDate()
      }
      return year + '' + month + day
    },
    goQjxxXq(qjxx) {
      this.$router.push({
        name: 'QjxxXq',
        params: {
          qjxx: qjxx
        }
      })
    },
    deleteQjxx(qjxx) {
      this.$store.dispatch('erp/deleteQjxxAction', {
        id: qjxx.id
      }).then(res => {
        this.$toast('删除成功！')
        this.$refs.commonlist.onRefresh()
      }).catch(error => [
        this.$toast(error)
      ])
    },
    getPrizeLog(date) {
      this.qjxxList = date
      for (let i = 0; i < this.qjxxList.length; i++) {
        var arr = this.qjxxList[i].qjlx.split(',')
        var name = ''
        console.log('arr==========================================================', arr)
        for (let j = 0; j < arr.length; j++) {
          if (j !== arr.length - 1) {
            name += this.qjlxMap[arr[j]] + '+'
          } else {
            name += this.qjlxMap[arr[j]]
          }
        }
        this.qjxxList[i].title = name
      }
    },
    getQjxx() {
      this.list.postUrl = 'erp/getQjxxAction'
      this.list.reqData = {
        ygbh: this.$store.state.mh.mhUserInfo.id,
        fqsj: this.fqsj,
        qjbh: '',
        zt: ''
      }
      this.list.dataName = 'xz_qjsqs'
      this.list.noDataText = '暂无数据'
    },
    getSynj() {
      this.$store.dispatch('erp/getSynjAction', {
        ygbh: this.$store.state.mh.mhUserInfo.id,
        nf: ''
      }).then(res => {
        this.synj = res
      }).catch(error => {
        console.log(error)
      })
    },
    onChange1() {

    },
    onChange2() {

    },
    onQjlxConfirm(leixing) {
      this.leixing = leixing
      this.QjlxShow = false
    },
    onQjztConfirm(zhuangtai) {
      this.zhuangtai = zhuangtai
      this.QjztShow = false
    },
    showSearchPopup() {
      this.SeachShow = true
    },
    goHome() {
      this.$router.back()
    },
    goZjQjxx() {
      this.$router.push({
        name: 'ZjQjxx'
      })
    },
    formDate(date) {
      date = date + ''
      return date.slice(0, 4) + '-' + date.slice(4, 6) + '-' + date.slice(6, 8)
    },
    showRemindBtn() {
      this.showRemind = !this.showRemind
    }
  }
}
</script>

<style scoped>
.page-list{margin: 15px 0;}
</style>
